import * as React from 'react';
import { graphql, useStaticQuery } from 'gatsby';

import SEO from '../components/seo';
import Markdown from 'react-markdown';

import classNames from 'classnames/bind';
import * as styles from './contact.module.scss';

const cx = classNames.bind(styles);

const Contact: React.FC = () => {
  const { mpapi } = useStaticQuery<GatsbyTypes.ContactQuery>(graphql`
    query Contact {
      mpapi {
        contactPage {
          data {
            attributes {
              content
            }
          }
        }
      }
    }
  `);

  if (!mpapi) return null;
  const contactPage = mpapi.contactPage.data.attributes;

  return (
    <>
      <SEO
        title={'Contact | Atelier Mendgen Peissl'}
        description="Contact page for architects Anna Mendgen und Dirk Peissl."
        image="https://mendgenpeissl.com/logo.jpg"
      />
      <h1>Contact</h1>
      <section className={cx('content')}>
        {contactPage.content && <Markdown>{contactPage.content}</Markdown>}
      </section>
    </>
  );
};

export default Contact;
